'use client';

import { useRef } from 'react';
import { Button } from '@/components/ui/button';
import { useSkills } from '@/hooks/use-skills';
import { useToast } from '@/hooks/use-toast';
import { Download, Upload, Box } from 'lucide-react';
import { ALL_SKILLS } from '@/lib/skills';

export default function AppHeader() {
  const { skills, setSkills } = useSkills();
  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleExport = () => {
    const dataStr = JSON.stringify(skills, null, 2);
    const blob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `web3skills-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    toast({ title: 'Success', description: 'Your skill data has been exported.' });
  };

  const handleImportClick = () => {
    fileInputRef.current?.click();
  };

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const content = e.target?.result;
          if (typeof content === 'string') {
            const importedSkills = JSON.parse(content);
            // Basic validation
            if (typeof importedSkills === 'object' && importedSkills !== null) {
              const validSkills: { [key: string]: boolean } = {};
              for (const skillName of Object.keys(ALL_SKILLS)) {
                if (importedSkills.hasOwnProperty(skillName) && typeof importedSkills[skillName] === 'boolean') {
                  validSkills[skillName] = importedSkills[skillName];
                } else {
                  validSkills[skillName] = false;
                }
              }
              setSkills(validSkills);
              toast({ title: 'Success', description: 'Your skill data has been imported.' });
            } else {
              throw new Error('Invalid file format.');
            }
          }
        } catch (error) {
          toast({ variant: 'destructive', title: 'Error', description: 'Failed to import file. Please check the file format.' });
        }
      };
      reader.readAsText(file);
    }
    // Reset file input value to allow importing the same file again
    if (fileInputRef.current) {
        fileInputRef.current.value = '';
    }
  };

  return (
    <header className="sticky top-0 z-10 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center">
        <div className="mr-4 flex items-center">
          <Box className="h-6 w-6 mr-2 text-primary" />
          <h1 className="text-xl font-bold tracking-tight">web3skills</h1>
        </div>
        <div className="flex flex-1 items-center justify-end space-x-2">
            <Button variant="outline" size="sm" onClick={handleImportClick}>
                <Upload className="mr-2" />
                Import
            </Button>
            <Button variant="outline" size="sm" onClick={handleExport}>
                <Download className="mr-2" />
                Export
            </Button>
            <input
                type="file"
                ref={fileInputRef}
                className="hidden"
                accept=".json"
                onChange={handleFileChange}
            />
        </div>
      </div>
    </header>
  );
}
