'use client';

import { useState } from 'react';
import AppHeader from "@/components/app-header";
import { AllSkills } from '@/components/all-skills';
import { Careers } from '@/components/careers';
import { Dashboard } from '@/components/dashboard';

export type View = 'home' | 'all-skills' | 'careers' | 'career-detail';

export default function Home() {
  const [currentView, setCurrentView] = useState<View>('home');
  const [selectedCareer, setSelectedCareer] = useState<string | null>(null);

  const renderView = () => {
    switch (currentView) {
      case 'home':
        return <Dashboard setCurrentView={setCurrentView} />;
      case 'all-skills':
        return <AllSkills setCurrentView={setCurrentView} />;
      case 'careers':
        return <Careers setCurrentView={setCurrentView} setSelectedCareer={setSelectedCareer} />;
      case 'career-detail':
        return <Careers setCurrentView={setCurrentView} setSelectedCareer={setSelectedCareer} selectedCareer={selectedCareer} />;
      default:
        return <Dashboard setCurrentView={setCurrentView} />;
    }
  };

  return (
    <div className="min-h-screen bg-background">
      <AppHeader />
      <main className="container mx-auto px-4 py-8">
        {renderView()}
      </main>
    </div>
  );
}
